import { Component } from 'react';
import PropTypes from 'prop-types';
import M from 'materialize-css';

const selections = [
    ["Choose", 0],
    ["20m", 20 * 60],
    ["30m", 30 * 60],
    ["1h", 1 * 60 * 60],
    ["2h", 2 * 60 * 60],
    ["3h", 3 * 60 * 60],
    ["4h", 4 * 60 * 60],
];

/* We don't need this anymore...?
const seconds_to_selections = {
    [20 * 60]: 1,
    [30 * 60]: 2,
    [1 * 60 * 60]: 3,
    [2 * 60 * 60]: 4,
    [3 * 60 * 60]: 5,
    [4 * 60 * 60]: 6,
};
*/

class Selection extends Component {
    constructor(props) {
        super(props);

        this.state = {
            selection: 0,
        };

        this.onSelect = this.onSelect.bind(this);
        this.reset = this.reset.bind(this);
    }

    componentDidMount() {
        if (this.input) {
            M.FormSelect.init(this.input);
        }
    }

    onSelect(event) {
        this.setState({
            selection: parseInt(event.target.value)
        }, () => {
            this.props.onChange(this.seconds());
        });
    }

    seconds() {
        return selections[this.state.selection][1];
    }

    reset() {
        this.setState({ selection: 0 });
    }

    render() {
        /* We don't need this anymore...?
        const selection = this.props.seconds ?
            seconds_to_selections[this.props.seconds]
            : this.state.selection;
        */

        return (
            <div className="input-field col">
                <select
                    className="white-text"
                    ref={(inp) => { this.input = inp; }}
                    value={this.props.selection}
                    onChange={this.onSelect}
                >
                    {selections.map((e, index) => (
                        <option
                            key={index}
                            value={index}
                            disabled={index === 0}
                        >{e[0]}</option>
                    ))}
                </select>
            </div>
        );
    }
}

Selection.propTypes = {
    onChange: PropTypes.func.isRequired,
};

export default Selection;