import { Component } from 'react';
import PropTypes from 'prop-types';

class Clock extends Component {
    constructor(props) {
        // Construct a Clock.
        super(props);

        // Initialize Clock state.
        this.state = {
            started: false,
            started_at: 0,
            elapsed: 0,
            i: 0, // Iteration
        };

        // Bind member functions to this instance.
        this.expire = this.expire.bind(this);
        this.iterate = this.iterate.bind(this);
        this.start = this.start.bind(this);
        this.stop = this.stop.bind(this);
    }

    expire(fn=() => {}) {
        // Set the Clock to expired state.
        // Calls fn() and this.props.onExpire after state is updated.
        this.setState({
            started: false,
            started_at: 0,
            elapsed: 0,
            i: 0,
        }, () => {
            fn();
            this.props.onExpire();
        });
    }

    iterate() {
        // Perform a single loop iteration.
        const {started_at} = this.state;
        const date = new Date();
        const now = date / 1000;

        this.setState({
            elapsed: now - started_at,
        }, () => {
            const seconds = this.props.seconds - this.state.elapsed;
            const minutes = seconds / 60;
            if (minutes <= 0) {
                this.expire(() => {
                    // Play the final alarm.
                    const audio = new Audio("/alarm.mp3");
                    audio.play();

                    // Stop the interval, we're done.
                    clearInterval(this.interval);
                });
            } else if (minutes <= 1) {
                // If there's a minute or less remaining, increment i and
                // play the alarm audio once a minute.
                this.setState({ i: this.state.i + 1 }, () => {
                    if (this.state.i % 60 === 0) {
                        // Play notification sound.
                        const audio = new Audio("/alarm.mp3");
                        audio.play();
                    }
                });
            } else if (minutes <= 5) {
                // If there are five minutes remaining, play the notification
                // audio once a minute. 
                this.setState({ i: this.state.i + 1 }, () => {
                    if (this.state.i % 60 === 0) {
                        // Play notification sound.
                        const audio = new Audio("/notify.mp3");
                        audio.play();
                    }
                });
            }
        });
    }

    start() {
        // Start the Clock, creating this.interval bound to this.iterate.
        const date = new Date();
        this.setState({
            started: true,
            started_at: date / 1000,
            elapsed: 0,
            i: 0,
        }, () => {
            this.interval = setInterval(this.iterate, 1000);
            this.props.onStart();
        });
    }

    stop(fn=() => {}) {
        // Stop the Clock, clearing this.interval.
        clearInterval(this.interval);
        this.setState({
            // Reset everything.
            started: false,
            started_at: 0,
            elapsed: 0
        }, () => {
            fn();
            this.props.onStop();
        });
    }

    render() {
        // Render the Clock.
        const seconds = this.props.seconds - this.state.elapsed;
        console.log("seconds: " + this.props.seconds);
        console.log("elapsed: " + this.state.elapsed);
        console.log(seconds);
        const date = new Date(null);
        date.setSeconds(seconds);
        const remaining = date.toISOString().substr(11, 5);

        return <span>{remaining}</span>;
    }
}

Clock.propTypes = {
    // Total number of timed seconds
    seconds: PropTypes.number.isRequired,

    // A callback invoked on start.
    onStart: PropTypes.func,

    // A callback invoked on stop.
    onStop: PropTypes.func,

    // A callback invoke on expiration.
    onExpire: PropTypes.func,
};

export default Clock;