import { Component } from 'react';
import PropTypes from 'prop-types';

class PlayButton extends Component {
    constructor(props) {
        super(props);
        this.state = {
            started: false,
        };

        this.start = this.start.bind(this);
        this.stop = this.stop.bind(this);
    }

    start() {
        this.setState({ started: true }, this.props.onPlay);
    }

    stop() {
        this.setState({ started: false }, this.props.onStop);
    }

    render() {
        const fn = this.state.started ? this.stop : this.start;
        const cls = this.state.started ? "fa-stop" : "fa-play";

        return (
            <div className="inline-box">
                <i className={`fa ${cls} play-button`} onClick={fn}></i>
            </div>
        );
    }
}

PlayButton.propTypes = {
    onPlay: PropTypes.func.isRequired,
    onStop: PropTypes.func.isRequired
};

export default PlayButton;