import { Component } from 'react';
import PropTypes from 'prop-types';
import Counter from './Counter';
import Title from './Title';

/*
const customTimerKey = (index) => {
    return "custom-timer-" + index;
};
*/

class Timer extends Component {
    constructor(props) {
        super(props);

        // Initialize state.
        this.state = {
            title: '',
            seconds: 0,
            startingSoon: false,
        };

        // Bind handlers.
        this.editTitle = this.editTitle.bind(this);
        this.onSelection = this.onSelection.bind(this);
    }

    componentDidMount() {
    }

    saveLocal() {
    }

    editTitle() {
        if (this.title) {
            this.title.edit();
        }
    }

    onSelection(seconds) {
        const date = new Date();
        const offset = date.getMinutes();
        if (offset === 0) {
            this.setState({ startingSoon: true }, () => {
                setTimeout(() => { this.onSelection(seconds); }, 1000);
            });
            return;
        }

        if (seconds >= 1 * 60 * 60) {
            // If >= 1 hour.
            const hours = seconds / 60 / 60;
            seconds = ((60 - date.getMinutes()) * 60) + ((hours - 1) * 60 * 60);
        } else if (seconds === 30 * 60) {
            // If 30 minutes.
            if (offset > 30) {
                // If > 30 minutes.
                seconds = (60 - offset) * 60;
            } else {
                // If <= 30 minutes.
                seconds = (30 - offset) * 60;
            }
        } else if (seconds === 20 * 60) {
            // If 20 minutes.
            if (offset > 40) {
                // If > 40 minutes.
                seconds = (60 - offset) * 60;
            } else if (offset > 20) {
                // If > 20 minutes.
                seconds = (40 - offset) * 60;
            } else {
                // If <= 20 minutes.
                seconds = (20 - offset) * 60;
            }
        }

        // Remove a minute from the total seconds to allow for
        // user input and safe travels.
        seconds = seconds - 60 + date.getUTCSeconds();

        this.setState({ seconds: seconds, startingSoon: false }, () => {
            console.log("Updated state to " + (seconds / 60) + " minutes.");
            this.counter.play();
        });
    }

    render() {
        const title = this.state.title;

        let hours = this.state.hours;
        if (hours === 0) {
            hours = '';
        } else if (!this.state.hourEdit && parseInt(hours) < 10) {
            hours = '0' + parseInt(hours);
        }

        let mins = this.state.minutes;
        if (mins === 0) {
            mins = '';
        } else if (!this.state.minuteEdit && parseInt(mins) < 10) {
            mins = '0' + parseInt(mins);
        }

        return (
            <div className="card blue-grey darken-1">
                <div className="card-content white-text">
                    <div className="box">
                        <Title
                            ref={(inp) => { this.title = inp; }}
                            string={title}
                        />
                        <i className="fa fa-pencil float-right" onClick={this.editTitle}></i>
                    </div>
                    {this.state.startingSoon && <p>Starting soon...</p>}
                </div>

                <div className="card-action">
                    {this.state.hourEdit || this.state.minuteEdit ? (
                        <div>
                            {this.state.hourEdit ? (
                            <input type="text"
                                value={hours}
                                onChange={this.setHour}
                                onKeyDown={this.handleHourKey}
                                placeholder="00"
                                ref={(inp) => { this.hourEdit = inp; }}></input>
                            ) : (
                                <input type="text"
                                value={hours}
                                placeholder="00"
                                readOnly={true}></input>
                            )}
                            :
                            {this.state.minuteEdit && (
                                <input type="text"
                                    value={mins}
                                    onChange={this.setMinute}
                                    onKeyDown={this.handleMinuteKey}
                                    onBlur={this.handleMinuteBlur}
                                    placeholder="00"
                                    ref={(inp) => { this.minuteEdit = inp; }}></input>
                            )}
                        </div>
                    ) : (
                        <Counter
                            index={this.props.index}
                            seconds={this.state.seconds}
                            onSelection={this.onSelection}
                            selection={this.state.selection}
                            ref={(inp) => { this.counter = inp; }}
                        />
                    )}
                </div>
            </div>
        );
    }
}

Timer.propTypes = {
    index: PropTypes.number.isRequired,
}

export default Timer;