import { Component } from 'react';
import PropTypes from 'prop-types';
import Clock from './Clock';
import Status from './Status';
import PlayButton from './PlayButton';
import Selection from './Selection';

class Counter extends Component {
    constructor(props) {
        // Construct a Counter.
        super(props);

        // Initialize state.
        this.state = {
            started: false,
            done: false,
            selection: 0,
        };

        // Bind Counter/Clock functions.
        this.expire = this.expire.bind(this);
        this.start = this.start.bind(this);
        this.play = this.play.bind(this);
        this.stop = this.stop.bind(this);

        // Bind PlayButton functions.
        this.onPlay = this.onPlay.bind(this);
        this.onStop = this.onStop.bind(this);
        
        this.onSelect = this.onSelect.bind(this);
        this.onStatusClick = this.onStatusClick.bind(this);
    }

    expire() {
        // Clock/Counter state trigger.
        this.setState({
            selection: 0,
            started: false,
            done: true
        }, this.playButton.stop);        
    }

    start() {
        // Clock/Counter state trigger.
        this.setState({ started: true });
    }

    play() {
        this.playButton.start();
    }

    stop() {
        // Clock/Counter state trigger.
        this.setState({ selection: 0, started: false });
    }

    onPlay() {
        // PlayButton handler.
        if (this.clock) {
            this.clock.start();
        }
    }

    onStop() {
        // PlayButton handler.
        if (this.clock) {
            this.clock.stop(() => {
                if (this.selection) {
                    this.selection.reset();
                }
            });
        }
    }

    onSelect(seconds) {
        this.props.onSelection(seconds);
    }

    onStatusClick() {
        this.setState({ started: false, done: false });
    }

    render() {
        const selection = (
            <Selection
                ref={(inp) => { this.selection = inp; }}
                seconds={this.props.seconds}
                selection={this.state.selection}
                onChange={this.onSelect}
            />
        );
        return (
            <div className="countdown flex">
                <Clock
                    ref={(inp) => { this.clock = inp; }}
                    seconds={this.props.seconds}
                    onStart={this.start}
                    onStop={this.stop}
                    onExpire={this.expire}
                />
                <div className="flex" />
                <Status
                    ref={(inp) => { this.status = inp; }}
                    started={this.state.started}
                    done={this.state.done}
                    placeholder={selection}
                    onClick={this.onStatusClick}
                />
                <div className="flex" />
                <PlayButton
                    ref={(inp) => { this.playButton = inp; }}
                    onPlay={this.onPlay}
                    onStop={this.onStop}
                />
            </div>
        );
    }
}

Counter.propTypes = {
    index: PropTypes.number.isRequired,
    selection: PropTypes.number.isRequired,
    seconds: PropTypes.number.isRequired,

    // Duration selection handler, takes a single integer seconds.
    onSelection: PropTypes.func.isRequired,
};

export default Counter;