import { Component } from 'react';
import PropTypes from 'prop-types';

class Status extends Component {
    render() {
        const {started, done, placeholder} = this.props
        if (started | done) {
            // If Status.props.started or Status.props.done is set.
            const element = !done ? (
                <div className="dot-elastic" />
            ) : (
                <i className="fa fa-check" onClick={this.props.onClick} />
            );
            return element;
        }
        return placeholder;
    }
}

Status.propTypes = {
    // If Status.props.started is true, a loading icon is displayed.
    started: PropTypes.bool.isRequired,

    // If Status.props.done is true, a check is displayed in green.
    done: PropTypes.bool.isRequired
};

export default Status;