import { Component } from 'react';
import PropTypes from 'prop-types';

export class TitleInput extends Component {
    constructor(props) {
        super(props);

        this.onKeyDown = this.onKeyDown.bind(this);
    }

    focus() {
        if (this.input) {
            this.input.focus();
        }
    }

    blur() {
        if (this.input) {
            this.input.blur();
        }
    }

    onKeyDown(event) {
        const key = event.key;
        if (key === 'Enter' || key === 'Escape' || key === 'Tab') {
            this.props.onBlur();
        }
    }

    render() {
        return <input
            ref={(inp) => { this.input = inp; }}
            className={this.props.className}
            type="text"
            value={this.props.value}
            onChange={this.props.onChange}
            onKeyDown={this.onKeyDown}
            onBlur={this.props.onBlur}
            placeholder={"Alarm title"}
            autoFocus={true}
        />;
    }
}

TitleInput.propTypes = {
    className: PropTypes.string.isRequired,
    value: PropTypes.string.isRequired,
    onChange: PropTypes.func.isRequired,
    onBlur: PropTypes.func.isRequired,
};

TitleInput.defaultProps = {
    className: ""
};

class Title extends Component {
    constructor(props) {
        // Construct a Title.
        super(props);

        // Initialize state with props.string. From then on,
        // we handle the title change internally.
        this.state = {
            title: this.props.string,
            editing: false,
        };

        // Bind triggers.
        this.edit = this.edit.bind(this);

        // Bind handlers.
        this.onChange = this.onChange.bind(this);
        this.onBlur = this.onBlur.bind(this);
    }

    edit() {
        this.setState({ title: '', editing: true });
    }

    onChange(event) {
        this.setState({ title: event.target.value });
    }

    onBlur() {
        // This handler gets called whenever the user performs an action
        // which completes or unfocuses the input.
        this.setState({ editing: false });
    }

    render() {
        let title = this.state.title;
        if (title === '') {
            title = 'N/A';
        }

        const element = this.state.editing ? (
            <TitleInput
                className="card-title inline"
                value={this.state.title}
                onChange={this.onChange}
                onBlur={this.onBlur}
            />
        ) : (
            <h3 className="card-title inline">{title}</h3>
        );
        return (
            <span className="card-title-wrapper">
                {element}
            </span>
        );
    }
}

Title.propTypes = {
    string: PropTypes.string.isRequired,
};

export default Title;